html, body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  color: #ff1493;
}

* {
  box-sizing: border-box;
}

a,
a:visited {
  text-decoration: none;
  color: #00a7e1;
}

a:hover,
a:focus,
a:active {
  text-decoration: underline;
}

.lock-scroll {
  overflow: hidden !important;
}

.header {
  padding: 0.5rem 1rem;
  color: #fff;
  background: #2e2e2e;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header h1 {
  margin: 0;
}

.header .header-nav {
  margin-left: auto;
}

.header .header-nav ul {
  margin: 0;
  padding: 0;
}

.header nav ul li {
  list-style: none;
}

.header .header-nav-link {
  text-decoration: none;
  color: #fff;
  border: solid 1px #fff;
  border-radius: 0.25rem;
  padding: 0.25rem 0.5rem;
  transition: all 0.2s;
}

.header .header-nav-link:focus,
.header .header-nav-link:hover {
  background: #fff;
  color: #2e2e2e;
}

@media screen and (max-width: 30em) {
  .header {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    padding: 1.5rem 1rem 1rem 1rem;
  }

  .header h1 {
    text-align: center;
  }

  .header .header-nav {
    margin-bottom: 1rem;
  }
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #2e2e2e;
  color: #fff;
  text-align: center;
  padding: 1rem;
}

.full-page {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #2e2e2e;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
}

.full-page h1 {
  color: #ff1493;
  font-size: 4rem;
  text-align: center;
}

.full-page p {
  margin: 0.5rem;
}

@media screen and (max-width: 30rem) {
  .full-page {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }

  .full-page h1 {
    font-size: 3rem;
  }
}

.home-page .home-page-nav {
  margin-top: 3rem;
}

.home-page .home-page-nav ul {
  padding: 0;
  margin: 0;
}

.home-page .home-page-nav ul li {
  list-style: none;
  padding: 1rem;
}

.loading {
  color: #ff1493;
  font-weight: bold;
}

.code-splitting-demo-2 button {
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

.context-demo-1 button {
  margin-bottom: 0.5rem;
}

.themed-section {
  margin-bottom: 0.5rem;
}

.component-error-boundary {
  color: #f00;
}

.component-error-boundary .pre-format {
  white-space: pre-wrap;
}


.function-as-child-components-page .example-section {
  margin-bottom: 1rem;
}

.function-as-child-components-page .example-section button {
  margin-bottom: 0.5rem;
  margin-right: 0.5rem;
}

.higher-order-components-page button {
  margin-bottom: 0.5rem;
}

.hooks-demo-1 button {
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

.left-indent {
  margin-left: 1rem;
}

.hooks-demo-4 .state-data {
  margin-bottom: 1rem;
}

.hooks-demo-4 .state-data h4 {
  margin: 0;
}

.lifecycle-methods-demo-1 button,
.lifecycle-methods-demo-2 button,
.lifecycle-methods-demo-3 button {
  margin-bottom: 0.5rem;
}

.lifecycle-methods-demo-1 .demo1-parent,
.lifecycle-methods-demo-1 .demo1-child,
.lifecycle-methods-demo-2 .bigger-container,
.lifecycle-methods-demo-2 .smaller-container {
  border: 1px solid #000;
  border-radius: 0.25rem;
  padding: 0.5rem;
}

.modal-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 999;
}

.modal {
  padding: 3rem 2rem 2rem 2rem;
  border: solid 1px #000;
  border-radius: 0.25rem;
  background: #fff;
  position: relative;
  z-index: 1000;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 80%;
  max-height: 80%;
}

.modal .modal-x-button {
  position: absolute;
  top: 0.25rem;
  right: 0.25rem;
}

.modal .modal-main-content {

}

.modal .modal-close-button {
  -webkit-align-self: center;
          align-self: center;
  margin-top: 2rem;
}

.modal-header {
  margin: 0;
  text-align: center;
}

.portals-page li {
  margin: 0.5rem auto;
}

.refs-demo-1 button {
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

.refs-demo-3 button:last-child {
  margin-top: 0.5rem;
}

.render-props-page .example-section {
  margin-bottom: 1rem;
}

.render-props-page .example-section button {
  margin-bottom: 0.5rem;
  margin-right: 0.5rem;
}

.page {
  padding: 2rem;
  margin-top: 2rem;
  margin-bottom: 6rem;
}

.introduction {
  margin-bottom: 2rem;
}

.demo-section {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.add-border {
  border: 1px solid #000;
  border-radius: 0.25rem;
  padding: 0.5rem;
}

h3 {
  color: #ff1493;
}

label {
  display: block;
  margin-bottom: 0.5rem;
}

input[type="text"],
input[type="number"] {
  border-radius: 0.25rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  border: 1px solid #000;
  padding: 0.25rem;
  margin-bottom: 0.5rem;
  font-size: 100%;
 }

button {
  border-radius: 0.25rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  padding: 0.25rem 0.5rem;
  font-size: 100%;
  cursor: pointer;
  border: solid 1px #2e2e2e;
  background: #2e2e2e;
  color: #fff;
}

button:focus,
button:hover {
  background: #fff;
  color: #2e2e2e;
}

@media screen and (max-width: 30em) {
  .page {
    margin-top: 7rem;
  }
}

